module.exports = "<span></span>\n<h2 class=\"ui header\">\n  <i class=\"percent icon\"></i>\n  Set completion\n</h2>\n<h3 class=\"ui header\">\n  By rarity\n</h3>\n<table class=\"ui definition stack-with-columns table\">\n  <thead>\n  <tr>\n    <th></th>\n    <th *ngFor=\"let field of completionFields\">{{ field.name | prTitleCase }}</th>\n  </tr>\n  </thead>\n  <tbody>\n  <tr *ngFor=\"let rarity of rarities\">\n    <td>{{ (long(rarity) || rarity) | prTitleCase }}</td>\n    <td *ngFor=\"let field of completionFields\">\n      <strong class=\"inline-heading\">{{ field.name | prTitleCase }}: </strong>\n      {{ (ss.completion.byRarity | async)[rarity][field.prop] }}\n      <span class=\"percentage\">{{ getCompletionPercentage((ss.completion.byRarity | async)[rarity], field.prop) }}</span>\n    </td>\n  </tr>\n  </tbody>\n</table>\n<h3 class=\"ui header\">\n  By class\n</h3>\n<table class=\"ui definition stack-with-columns table\">\n  <thead>\n  <tr>\n    <th></th>\n    <th *ngFor=\"let field of completionFields\">{{ field.name | prTitleCase }}</th>\n  </tr>\n  </thead>\n  <tbody>\n  <tr *ngFor=\"let klass of classes | async\">\n    <td>{{ klass | prTitleCase }}</td>\n    <td *ngFor=\"let field of completionFields\">\n      <strong class=\"inline-heading\">{{ field.name | prTitleCase }}: </strong>\n      {{ (ss.completion.byClass | async)[klass][field.prop] }}\n      <span class=\"percentage\">{{ getCompletionPercentage((ss.completion.byClass | async)[klass], field.prop) }}</span>\n    </td>\n  </tr>\n  </tbody>\n</table>\n<h2 class=\"ui header\">\n  <i class=\"clone icon\"></i>\n  Card opening stats\n</h2>\n<table class=\"ui definition stack-with-columns table\">\n  <thead>\n  <tr>\n    <th></th>\n    <th *ngFor=\"let field of cardFields\">{{ field.name | prTitleCase }}</th>\n  </tr>\n  </thead>\n  <tbody>\n  <tr *ngFor=\"let rarity of rarities\">\n    <td>{{ (long(rarity) || rarity) | prTitleCase }}</td>\n    <td *ngFor=\"let field of cardFields\">\n      <strong class=\"inline-heading\">{{ field.name | prTitleCase }}: </strong>\n      {{ (ss.card | async)[rarity][field.prop] }}\n      <span class=\"percentage\">{{ getCardPercentage(ss.card | async, rarity, field.prop) }}</span>\n    </td>\n  </tr>\n  </tbody>\n</table>\n<pr-legend><p>\n  Percentages in the Total and Regular columns are relative to all cards opened.\n  That is, if you opened 100 cards of which 70 are common then Total &gt; Common would show (70%).<br/>\n  The Golden column shows percentages relative to the total of the same rarity.\n  For example, if you opened 10 rares of which 2 are golden then Golden &gt; Rare would show (20%).<br/>\n  The Extra columns (Regular Extra and Golden Extra) show how many cards you can freely disenchant because you have\n  more of that card than you can put in a deck. In these columns the percentages are relative to the same rarity.\n  That is, if you opened 3 golden epics of which 1 is extra, then Golden Extra &gt; Epic would show (33.3%).\n</p></pr-legend>\n<h2 class=\"ui header\">\n  <i class=\"certificate icon\"></i>\n  Dust stats\n</h2>\n<table class=\"ui definition stack-with-columns table\">\n  <thead>\n  <tr>\n    <th></th>\n    <th *ngFor=\"let field of dustFields\">{{ field.name | prTitleCase }}</th>\n  </tr>\n  </thead>\n  <tbody>\n  <tr *ngFor=\"let rarity of rarities\">\n    <td>{{ (long(rarity) || rarity) | prTitleCase }}</td>\n    <td *ngFor=\"let field of dustFields\">\n      <strong class=\"inline-heading\">{{ field.name | prTitleCase }}: </strong>\n      {{ (ss.dust | async)[rarity][field.prop] }}\n    </td>\n  </tr>\n  </tbody>\n</table>\n";

